@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.page {
  position: relative;
  background-color: #FCDA05;
  min-height: 100vh;
  font-family: 'Montserrat',
    sans-serif;
  color: #5c3551;
}

.page__header {
  display: flex;
}

.heading__logo {
  width: 100px;
}

@media(max-width: 600px) {
  .heading__logo {
    width: 80px;
  }
}

@media(max-width: 400px) {
  .heading__logo {
    width: 70px;
  }
}

.page__heading {
  flex-grow: 1;
  text-align: center;
  line-height: 100px;
  font-size: 26px;
  -webkit-transform: translateX(-50px);
          transform: translateX(-50px);
}

@media(max-width: 600px) {
  .page__heading {
    font-size: 20px;
    line-height: 80px;
    -webkit-transform: translateX(-25px);
            transform: translateX(-25px);
  }
}

@media(max-width: 400px) {
  .page__heading {
    font-size: 16px;
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
    line-height: 70px;
  }
}

.page__navigation {
  position: absolute;
  width: 100px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  text-align: center;
}

.nav__link {
  text-decoration: none;
  color: inherit;
  line-height: 30px;
  font-size: 20px;
  position: relative;
}

@media(max-width: 600px) {
  .nav__link {
    font-size: 16px;
  }
}

@media(max-width: 400px) {
  .nav__link {
    font-size: 14px;
  }
}

.nav__link::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 3px;
  border-radius: 3px;
  top: calc(100% - 3px);
  left: 0;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  transition: -webkit-transform .2s linear;
  transition: transform .2s linear;
  transition: transform .2s linear, -webkit-transform .2s linear;
  background-color: #5c3551;
}

.nav__link:hover::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}
.pets__back {
    position: absolute;
    top: 5%;
    left: 10%;
    width: 50px;
    font-size: 25px;
    line-height: 25px;
    color: rgba(92, 53, 81, 0.5);
    transition: color .2s, border-color .2s;
    border: 2px solid rgba(92, 53, 81, 0.5);
    border-radius: 5px;
    text-align: center;
    overflow: hidden;
}

.pets__back:hover {
    color: #5c3551;
    border-color: #5c3551;
}

.pets__back:hover .fa-angle-left {
    text-shadow: -10px 0 0 #5c3551, 10px 0 0 #5c3551;
}

.pets__back:hover {
    color: #5c3551;
}

.pets__heading {
    padding-top: 50px;
    text-align: center;
}

.pets__loader {
    position: relative;
    width: 50px;
    height: 4px;
    margin: 30px auto;
    border-radius: 5px;
    background-color: rgba(92, 53, 81, 0.5);
}

.pets__loader::after {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    border-radius: 5px;
    background-color: #5c3551;
    top: 0;
    left: 0;
    -webkit-transform-origin: left center;
            transform-origin: left center;
    -webkit-animation: loading 1.5s linear forwards;
            animation: loading 1.5s linear forwards;
}

@-webkit-keyframes loading {
    from {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
    }

    to {
        -webkit-transform: scaleX(0);
                transform: scaleX(0);
    }
}

@keyframes loading {
    from {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
    }

    to {
        -webkit-transform: scaleX(0);
                transform: scaleX(0);
    }
}

.pets__section {
    display: grid;
    grid-template-columns: repeat(4, 125px);
    grid-template-rows: 180px;
    grid-auto-rows: 180px;
    grid-row-gap: 10px;
    justify-content: space-evenly;
    padding: 25px 30px;
    max-width: 1000px;
    margin: 0 auto;
}

@media(max-width: 600px) {
    .pets__section {
        grid-template-columns: repeat(3, 125px);
        padding: 20px 25px;
    }
}

@media(max-width: 400px) {
    .pets__section {
        grid-template-columns: repeat(2, 125px);
    }
}

.pet__item {
    display: flex;
    flex-direction: column;
    height: 100%;
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
    opacity: 0;
}

.pet__item.active {
    -webkit-animation: left .5s forwards;
            animation: left .5s forwards;
}

@-webkit-keyframes left {
    from {
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
        opacity: 0;
    }

    to {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
    }
}

@keyframes left {
    from {
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
        opacity: 0;
    }

    to {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
    }
}

.image__wrapper {
    border-radius: 90px;
    flex-basis: 125px;
    overflow: hidden;
}

.pet__img {
    height: 100%;
    width: 100%;
    cursor: pointer;
}

.fas {
    margin-left: 20px;
}

.fa-arrow-alt-circle-left {
    margin-left: 0;
}

.vaccinated {
    color: rgba(255, 0, 0, 0.6);
}

.pet__btn {
    position: absolute;
    top: 10%;
    right: 10%;
}

.pet__name {
    margin-top: 5px;
}

.pet__date {
    font-size: 12px;
}
.modal {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}

.modal__wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 50%;
    min-width: 300px;
    max-width: 700px;
    border-radius: 20px;
    box-shadow: 0 0 15px 3px black;
}

.modal__image {
    width: 100%;
    border-radius: 20px;
    display: block;
}

.modal__btn {
    position: absolute;
    top: 3%;
    right: 3%;
    font-size: 30px;
    color: #5c3551;
    border-style: none;
    background-color: transparent;
    cursor: pointer;
    transition: color .2s;
}

.modal__btn .fa-times-circle {
    margin: 0;
    display: block;
}

.modal__btn:hover {
    color: #FCDA05;
}

.events__left,
.events__right {
    position: absolute;
    top: 50%;
    left: 0;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background-color: transparent;
    border-style: none;
    font-size: 25px;
    color: #5c3551;
    cursor: pointer;
    transition: color .2s;
}

.events__left:hover,
.events__right:hover {
    color: #FCDA05;
}

.events__left .fa-arrow-circle-left,
.events__right .fa-arrow-circle-right {
    margin: 0;
    display: block;
}

.events__right {
    left: auto;
    right: 0;
}
.events__wrapper {
    margin: 0 auto;
    max-width: 1000px;
    height: 100%;
}

.events__back {
    position: absolute;
    top: 5%;
    left: 10%;
    width: 50px;
    font-size: 25px;
    line-height: 25px;
    color: rgba(92, 53, 81, 0.5);
    transition: color .2s, border-color .2s;
    border: 2px solid rgba(92, 53, 81, 0.5);
    border-radius: 5px;
    text-align: center;
    overflow: hidden;
}

.fa-angle-left {
    margin-left: 0;
    text-shadow: -10px 0 0 rgba(92, 53, 81, 0.5), 10px 0 0 rgba(92, 53, 81, 0.5);
    -webkit-animation: move 2s infinite linear;
            animation: move 2s infinite linear;
    transition: text-shadow .2s;
}

@-webkit-keyframes move {
    from {
        -webkit-transform: translateX(50px);
                transform: translateX(50px);
    }

    to {
        -webkit-transform: translateX(-50px);
                transform: translateX(-50px);
    }
}

@keyframes move {
    from {
        -webkit-transform: translateX(50px);
                transform: translateX(50px);
    }

    to {
        -webkit-transform: translateX(-50px);
                transform: translateX(-50px);
    }
}

.events__back:hover {
    color: #5c3551;
    border-color: #5c3551;
}

.events__back:hover .fa-angle-left {
    text-shadow: -10px 0 0 #5c3551, 10px 0 0 #5c3551;
}

.events__heading {
    padding-top: 50px;
    text-align: center;
}

@media(max-width: 450px) {
    .events__heading {
        font-size: 20px;
    }
}

.events__loader {
    position: relative;
    width: 50px;
    height: 4px;
    margin: 30px auto;
    border-radius: 5px;
    background-color: rgba(92, 53, 81, 0.5);
}

.events__loader::after {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    border-radius: 5px;
    background-color: #5c3551;
    top: 0;
    left: 0;
    -webkit-transform-origin: left center;
            transform-origin: left center;
    -webkit-animation: loading 1.5s linear forwards;
            animation: loading 1.5s linear forwards;
}

.event {
    padding: 15px 10px;
    -webkit-animation: visible 1s both;
            animation: visible 1s both;
}

.event:nth-of-type(2) {
    -webkit-animation-delay: .5s;
            animation-delay: .5s;
}

@-webkit-keyframes visible {
    from {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
        opacity: 0;
    }

    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@keyframes visible {
    from {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
        opacity: 0;
    }

    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

.event__heading {
    text-align: center;
}

@media(max-width: 600px) {
    .event__heading {
        font-size: 16px;
    }
}

@media(max-width: 450px) {
    .event__heading {
        font-size: 14px;
    }
}

.event__date {
    text-align: center;
}

@media(max-width: 600px) {
    .event__date {
        font-size: 14px;
    }
}

@media(max-width: 450px) {
    .event__date {
        font-size: 12px;
    }
}

.event__images {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}

.event__image {
    width: 18%;
    border-radius: 10px;
    box-shadow: 0 0 10px black;
    cursor: pointer;
}

@media(max-width: 600px) {
    .event__image {
        width: 19%;
    }
}
